.comment .comment__img { float: left; width: 100px; height: 100px; margin-right: 25px; border-radius: 50%; overflow: hidden; font-size: 0; }
.comment .comment__img img { width: 100%; height: 100%; }
.comment .comment__user { padding-top: 15px; overflow: hidden; font-size: 15px; color: #333; }
.comment .comment__user small { font-size: 14px; color: #999; }
.comment .comment__content { margin-bottom: 25px; font-size: 14px; color: #333; }
.comment .comment__content a { color: #2b2f4e; }
.comment .comment__bottom { font-size: 12px; color: #2b2f4e; }

.results { background: #f7f7f7; margin-bottom: 20px; }
.results .results__img { font-size: 0; margin-bottom: 20px; }
.results .results__img img { width: 100%; }
.results .results__in { padding: 30px 15px; font-size: 14px; line-height: 1.4; color: #333; margin-bottom: 20px; }
.results .results__in h1, .results .results__in h2, .results .results__in h3, .results .results__in h4, .results .results__in h5, .results .results__in h6 { color: #333; }
.results .results__in p { margin-bottom: 25px; }
.results .results__in a { color: #333; }
.results .results__in h5 { text-transform: uppercase; margin-top: 30px; }
@media only screen and (max-width: 767px) { .results .results__in { padding: 10px 0; } }

.images { font-size: 0; }
.images a { display: inline-block; width: 16.666%; }
.images a img { width: 100%; }


